@use 'base/variables' as v;
@use 'base/mixins' as mx;

.header{
    background-color: v.$verde;

    @include mx.screen(v.$tablet){
      
          &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #666;
          }
    }

    h1{
        color: v.$blanco;
        text-align: center;

        @include mx.screen(v.$tablet){
          margin: 0;
        }
    }

    .contenedor-header{
        padding: 2rem;

        @include mx.screen(v.$tablet){
          display:flex;
          justify-content: space-between; 
          align-items: center;
        }
        
    }

}

.navegacion-principal{

    @include mx.screen(v.$tablet){
      display: flex;
      gap: 2rem;
    }

    a{
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;


        &:hover{
            color: v.$amarillo
        }

        @include mx.screen(v.$tablet){
          margin: 0;
        }
    }

}
