@use 'base/variables' as v;
@use 'base/mixins' as mx;



.sobre-festival{
   padding: 5rem 0;
   
   @include mx.screen(v.$tablet){     
      @include mx.grid(2, 5rem);
   }
}

.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;

    margin-top: 2rem; 

    @include mx.screen(v.$tablet){
      margin-top: 0;
    }

    h2{
      margin-bottom: 2rem;
    }

    .fecha{
       color: v.$rosa;
       font-weight: 700;
    }

    p{
        margin-top: 2rem;
    }
}