/*  Globales */

@use 'variables' as v;

/* *,*::after,*::before{
    margin: 0;
    padding: 0;
    box-sizing: inherit;
} */

html{
    font-size: 62.5%;
    box-sizing: border-box;
}

body{
    margin: 0;
    font-size: 1.6rem;
    font-family: v.$fuente-principal;

    &.fijar-body{
        overflow: hidden;
    }

    &.body-scroll{
        padding-top: 0rem;
    }
}

p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 2rem;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1, h2, h3{
    margin: 0 0 5rem 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

li{
    list-style: none;
}

a{
    text-decoration: none;
}