@use 'base/variables' as v;
@use 'base/mixins' as mx;

.video{
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay{
        position: absolute;
        background: rgb(169,1,255);
        background: linear-gradient(270deg, rgba(169,1,255,0.35476197314863445) 0%, rgba(255,0,71,0.49761911600577735) 100%); 
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    video{
        width: auto;
        display: block;
        
        @include mx.screen(v.$desktop){
            width: 100%;
          
        }
    }
}

.contenido-video{
    z-index: 999;

    h2{
      color: v.$blanco;
      font-size: 7rem;
      margin: 0;
    }

    p{
      background-color: v.$rosa;
      display: inline-block;
      padding: 1rem 2rem;
      color: v.$blanco;
      font-weight: 700;
      font-size: 2.4rem;
      margin: 0;
    }
}