@use 'base/variables' as v;
@use 'base/mixins' as mx;

.boletos{
       padding: 5rem 0;

    h3{
       text-align: center;
       color: v.$blanco;
       margin-bottom: 2rem
    }

    .contenedor-boletos{
       @include mx.screen(v.$tablet){
          @include mx.grid(2, 5rem);
       }
        
    }
}

.pase{
    color: v.$negro;

    .nombre-pase{
        text-align: center;
        font-size: 2.8rem;
        color: v.$blanco;
    }

    &-header, &-footer{
        padding: .5rem;
        p{
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }
    }


    &-header{
       background-color: v.$rosa;

       p{
        text-transform: uppercase;
        font-size: 1.6rem;
        padding: 1rem;
       }
    }

    &-body{
        background-color: v.$blanco;

        ul{
            @include mx.resetear-lista;
            li, :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }

    &-footer{
        background-color: v.$morado;

        p{
            font-size: 1.8rem;
            font-weight: 700;
            padding: 1rem;
        }
    }
}